export default {
	data() {
		return {
			userInfo: {}
		}
	},
	onShow() {
		let userInfo = uni.getStorageSync('userInfo');
		this.userInfo = userInfo;
	},
	methods: {
		goSetPwd() {
			uni.navigateTo({
				url: 'setPwd/setPwd'
			})
		},
		goGetPwd() {
			uni.navigateTo({
				url: 'getPwd/getPwd'
			})
		},
		goNewPwd() {
			uni.navigateTo({
				url: 'newPwd/newPwd'
			})
		}
	}
}
